"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { AppLayout } from "@/components/app-layout"
import { ChevronLeft, ChevronRight, Plus, X } from "lucide-react"
import { getJapaneseHolidays } from "@/lib/japanese-holidays"

interface CalendarEvent {
  id: number
  title: string
  date: string
  color: string
  isHoliday?: boolean
}

const COLORS = [
  { name: "赤", value: "#ef4444" },
  { name: "青", value: "#3b82f6" },
  { name: "緑", value: "#22c55e" },
  { name: "黄", value: "#eab308" },
  { name: "紫", value: "#a855f7" },
  { name: "ピンク", value: "#ec4899" },
  { name: "オレンジ", value: "#f97316" },
]

export default function CalendarPage() {
  const [currentDate, setCurrentDate] = useState(new Date())
  const [events, setEvents] = useState<CalendarEvent[]>([])
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [selectedDate, setSelectedDate] = useState<string | null>(null)
  const [newEventTitle, setNewEventTitle] = useState("")
  const [newEventColor, setNewEventColor] = useState(COLORS[0].value)
  const [isLoading, setIsLoading] = useState(false)

  const getHolidayEvents = (): CalendarEvent[] => {
    const year = currentDate.getFullYear()
    const holidays = getJapaneseHolidays(year)

    // 前月と翌月の祝日も取得（カレンダー表示用）
    const prevYearHolidays = year > 2024 ? getJapaneseHolidays(year - 1) : []
    const nextYearHolidays = year < 2030 ? getJapaneseHolidays(year + 1) : []

    const allHolidays = [...prevYearHolidays, ...holidays, ...nextYearHolidays]

    return allHolidays.map((h, index) => ({
      id: -index - 1, // 祝日は負のIDを使用
      title: h.name,
      date: h.date,
      color: "#ef4444", // 赤色
      isHoliday: true,
    }))
  }

  // イベント取得
  const fetchEvents = async () => {
    try {
      const year = currentDate.getFullYear()
      const month = currentDate.getMonth() + 1
      const res = await fetch(`/api/calendar?year=${year}&month=${month}`)
      if (res.ok) {
        const data = await res.json()
        if (Array.isArray(data)) {
          const holidayEvents = getHolidayEvents()
          setEvents([...holidayEvents, ...data])
        }
      }
    } catch (error) {
      console.error("Failed to fetch events:", error)
      // エラー時でも祝日は表示
      setEvents(getHolidayEvents())
    }
  }

  useEffect(() => {
    fetchEvents()
  }, [currentDate])

  // 月を変更
  const changeMonth = (delta: number) => {
    setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + delta, 1))
  }

  // 今日に戻る
  const goToToday = () => {
    setCurrentDate(new Date())
  }

  // カレンダーの日付を生成
  const generateCalendarDays = () => {
    const year = currentDate.getFullYear()
    const month = currentDate.getMonth()
    const firstDay = new Date(year, month, 1)
    const lastDay = new Date(year, month + 1, 0)
    const startDay = firstDay.getDay()
    const daysInMonth = lastDay.getDate()

    const days: { date: Date; isCurrentMonth: boolean }[] = []

    // 前月の日付
    const prevMonthLastDay = new Date(year, month, 0).getDate()
    for (let i = startDay - 1; i >= 0; i--) {
      days.push({
        date: new Date(year, month - 1, prevMonthLastDay - i),
        isCurrentMonth: false,
      })
    }

    // 今月の日付
    for (let i = 1; i <= daysInMonth; i++) {
      days.push({
        date: new Date(year, month, i),
        isCurrentMonth: true,
      })
    }

    // 次月の日付
    const remainingDays = 42 - days.length
    for (let i = 1; i <= remainingDays; i++) {
      days.push({
        date: new Date(year, month + 1, i),
        isCurrentMonth: false,
      })
    }

    return days
  }

  // 日付をフォーマット
  const formatDateKey = (date: Date) => {
    return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, "0")}-${String(date.getDate()).padStart(2, "0")}`
  }

  // イベント追加モーダルを開く
  const openAddModal = (date: Date) => {
    setSelectedDate(formatDateKey(date))
    setNewEventTitle("")
    setNewEventColor(COLORS[0].value)
    setIsModalOpen(true)
  }

  // イベントを追加
  const addEvent = async () => {
    if (!newEventTitle.trim() || !selectedDate) return

    setIsLoading(true)
    try {
      const res = await fetch("/api/calendar", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          title: newEventTitle,
          date: selectedDate,
          color: newEventColor,
        }),
      })

      if (res.ok) {
        setIsModalOpen(false)
        fetchEvents()
      }
    } catch (error) {
      console.error("Failed to add event:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const deleteEvent = async (event: CalendarEvent, e: React.MouseEvent) => {
    e.stopPropagation()
    if (event.isHoliday) return // 祝日は削除不可
    if (!confirm("このイベントを削除しますか？")) return

    try {
      const res = await fetch(`/api/calendar/${event.id}`, { method: "DELETE" })
      if (res.ok) {
        fetchEvents()
      }
    } catch (error) {
      console.error("Failed to delete event:", error)
    }
  }

  const days = generateCalendarDays()
  const today = new Date()
  const todayKey = formatDateKey(today)
  const weekDays = ["日", "月", "火", "水", "木", "金", "土"]

  const holidayDates = new Set(events.filter((e) => e.isHoliday).map((e) => e.date))

  return (
    <AppLayout>
      <div className="min-h-screen bg-gray-50 p-4 md:p-6">
        {/* ヘッダー */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4 mb-4">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
            <div className="flex items-center gap-4">
              <button
                onClick={goToToday}
                className="px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium hover:bg-gray-50"
              >
                今日
              </button>
              <div className="flex items-center gap-2">
                <button onClick={() => changeMonth(-1)} className="p-2 hover:bg-gray-100 rounded-lg">
                  <ChevronLeft className="h-5 w-5" />
                </button>
                <button onClick={() => changeMonth(1)} className="p-2 hover:bg-gray-100 rounded-lg">
                  <ChevronRight className="h-5 w-5" />
                </button>
              </div>
              <h1 className="text-xl font-bold text-gray-900">
                {currentDate.getFullYear()}年{currentDate.getMonth() + 1}月
              </h1>
            </div>
          </div>
        </div>

        {/* カレンダー */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
          {/* 曜日ヘッダー */}
          <div className="grid grid-cols-7 border-b border-gray-200">
            {weekDays.map((day, index) => (
              <div
                key={day}
                className={`py-3 text-center text-sm font-medium ${
                  index === 0 ? "text-red-500" : index === 6 ? "text-blue-500" : "text-gray-700"
                }`}
              >
                {day}
              </div>
            ))}
          </div>

          {/* 日付グリッド */}
          <div className="grid grid-cols-7">
            {days.map((day, index) => {
              const dateKey = formatDateKey(day.date)
              const dayEvents = events.filter((e) => e.date === dateKey)
              const isToday = dateKey === todayKey
              const dayOfWeek = day.date.getDay()
              const isHoliday = holidayDates.has(dateKey)

              return (
                <div
                  key={index}
                  className={`min-h-[100px] md:min-h-[120px] border-b border-r border-gray-100 p-1 ${
                    !day.isCurrentMonth ? "bg-gray-50" : ""
                  }`}
                >
                  <div className="flex items-center justify-between mb-1">
                    <span
                      className={`text-sm w-7 h-7 flex items-center justify-center rounded-full ${
                        isToday
                          ? "bg-yellow-400 text-gray-900 font-bold"
                          : !day.isCurrentMonth
                            ? "text-gray-400"
                            : isHoliday || dayOfWeek === 0
                              ? "text-red-500"
                              : dayOfWeek === 6
                                ? "text-blue-500"
                                : "text-gray-700"
                      }`}
                    >
                      {day.date.getDate()}
                    </span>
                    {day.isCurrentMonth && (
                      <button
                        onClick={() => openAddModal(day.date)}
                        className="p-1 hover:bg-gray-100 rounded opacity-0 hover:opacity-100 transition-opacity"
                      >
                        <Plus className="h-4 w-4 text-gray-400" />
                      </button>
                    )}
                  </div>
                  <div className="space-y-1">
                    {dayEvents.slice(0, 3).map((event) => (
                      <div
                        key={event.id}
                        className={`group flex items-center gap-1 px-1.5 py-0.5 rounded text-xs truncate ${
                          event.isHoliday ? "bg-red-100 text-red-700" : "text-white cursor-pointer"
                        }`}
                        style={!event.isHoliday ? { backgroundColor: event.color } : undefined}
                      >
                        <span className="truncate flex-1">{event.title}</span>
                        {!event.isHoliday && (
                          <button
                            onClick={(e) => deleteEvent(event, e)}
                            className="opacity-0 group-hover:opacity-100 transition-opacity flex-shrink-0"
                          >
                            <X className="h-3 w-3" />
                          </button>
                        )}
                      </div>
                    ))}
                    {dayEvents.length > 3 && (
                      <div className="text-xs text-gray-500 px-1">+{dayEvents.length - 3}件</div>
                    )}
                  </div>
                </div>
              )
            })}
          </div>
        </div>

        {/* イベント追加モーダル */}
        {isModalOpen && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-lg shadow-xl w-full max-w-md">
              <div className="flex items-center justify-between p-4 border-b border-gray-200">
                <h2 className="text-lg font-bold text-gray-900">イベントを追加</h2>
                <button onClick={() => setIsModalOpen(false)} className="p-1 hover:bg-gray-100 rounded">
                  <X className="h-5 w-5 text-gray-500" />
                </button>
              </div>
              <div className="p-4 space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">日付</label>
                  <input
                    type="date"
                    value={selectedDate || ""}
                    onChange={(e) => setSelectedDate(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-yellow-400 focus:border-transparent"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">タイトル</label>
                  <input
                    type="text"
                    value={newEventTitle}
                    onChange={(e) => setNewEventTitle(e.target.value)}
                    placeholder="イベント名を入力"
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-yellow-400 focus:border-transparent"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">色</label>
                  <div className="flex gap-2 flex-wrap">
                    {COLORS.map((color) => (
                      <button
                        key={color.value}
                        onClick={() => setNewEventColor(color.value)}
                        className={`w-8 h-8 rounded-full border-2 ${
                          newEventColor === color.value ? "border-gray-900 scale-110" : "border-transparent"
                        }`}
                        style={{ backgroundColor: color.value }}
                        title={color.name}
                      />
                    ))}
                  </div>
                </div>
              </div>
              <div className="flex gap-3 p-4 border-t border-gray-200">
                <button
                  onClick={addEvent}
                  disabled={isLoading || !newEventTitle.trim()}
                  className="flex-1 py-2 bg-yellow-400 text-gray-900 font-semibold rounded-lg hover:bg-yellow-500 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  {isLoading ? "追加中..." : "追加"}
                </button>
                <button
                  onClick={() => setIsModalOpen(false)}
                  className="px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50"
                >
                  キャンセル
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </AppLayout>
  )
}
